.tictactoe {
    text-align: center;
    vertical-align:middle !important; 
}

.row{
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.players{
    display:flex;
    margin-right: -15px;
    margin-left: -15px;
    padding-top:30vh;
}

.title{
    font: 32px bold;
    height:10vh;
}



.cell{
    font: 128px bold;
    border: 1px solid black;
}

.form.container{
    width: 100%;
}

.errormessage{
    color: red;
    font-size: 16px;
    margin-top:10px;
}
